

html {
  font-size: 16px;
}
body {
  font-family: "Open Sans", sans-serif;
  line-height: 1;
}

.Root {
  height: 100vh;

}

.Tabs {
  display: grid;
  grid-template-columns: 30% auto;
}

.TabList {
  background: #ffffff;
  padding-top: 3%;
  overflow: hidden;
}
.left_tabs {
  padding-left: 61px;
  padding-bottom: 79px;
}

.TabPanel {
  flex: 1;
  display: none;

  float:right;

}
.TabPanel.is-active {
  display: block;
  background: #f7f8fa;
  padding: 108px 100px 50px 100px;
}
.Button {
  display: inline-block;
  /* width: 13%; */
  padding: 7px 15px;
    background: none;
    border: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 1rem;
    font-weight: bold;
    color: rgb(115 115 115);
    border-radius: 25px;

    background: #ffffff;
    border :1px solid #6B6B6A;
    border-radius: 25px;
    border: 1px solid;
    height: 40px;
}
.Button:focus,
.Button:hover {
  outline: 0;
  background: #899499;
}
.Tab.is-active .Button {
    background: #ffffff;
    color: #EE9821;
    border-radius: 25px;
    border: 2px solid;
}


.line{
width: 0px;
left: 72px;
border: 1px dashed #a0a0a0;
height: 40px;
margin-left: 21px;
}
.Tab.is-active .line{
border: 1px dashed #EE9821;
}

.but{
  background: #ffffff;
  border :1px solid #6B6B6A;
  border-radius: 25px;
  border: 2px solid;
}

.text2{
  font-family: Poppins;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #6B6B6A;
  margin: 7%;
      font-weight: 600;
    font-size: 16px;
}

.Tab.is-active .text2{

color: #EE9821;;
}



